@import './../../styles/index.scss';

.passportWrapper {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.1),
      rgba(0, 0, 0, 1)
    ),
    url('./../../assets//images/passport.jpg');
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  overflow: hidden;
  display: inline-block;
  background-color: $c-header;
  display: block;
}

.passport {
  padding: 30px;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 20px;

  @media screen and (max-width: $phone) {
    gap: 50px;
  }
}

.title {
  text-align: center;
  font-size: 38px;
  color: $c-white;
  text-transform: uppercase;
  transform: scale(1, 1.2);

  @media screen and (min-width: $phone) and (max-width: $desktop) {
    font-size: 28px;
  }

  @media screen and (max-width: $phone) {
    font-size: 22px;
  }
}

.subtitle {
  text-align: center;
  font-size: 20px;
  font-weight: normal;
  color: $c-white;

  @media screen and (max-width: $tablet) {
    font-size: 16px;
  }
}

.link {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
  background-color: $c-link-hover;
  padding: 6px 0 6px 18px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: opacity 0.3s ease;
  transition: $t-normal;

  @media screen and (max-width: $tablet) {
    padding: 5px 0 3px 16px;
  }
}

.link:hover {
  background-color: $c-link-active;
}
.link:active {
  background-color: rgb(23, 23, 23);
}

.animation {
  margin-top: 2px;
  width: 40px;
  height: 40px;
  transform: rotate(-90deg);

  @media screen and (max-width: $tablet) {
    margin-top: 0;
  }
}

.imgContainer {
  transform: rotate(180deg);
  overflow: hidden;
}

.imgContainer img {
  display: block;
  overflow: hidden;
}

.imgContainerSecond {
  overflow: hidden;
}

.imgContainerSecond img {
  display: block;
  overflow: hidden;
}
